<template>
  <div class="recharge-result-page">
    <div class="recharge-result" :class="{'result-success': status==1,'result-fail': status==2}">
      <div class="result-icon"></div>
      <p v-if="status==1">支付成功</p>
      <p v-else-if="status==2">支付失败</p>
    </div>
    <div class="result-warp">
      <div class="result-list">
        <div><span>充值号码</span><strong>{{mobile | mobileFormate}}</strong></div>
        <div><span>面值</span><strong>￥{{money}}</strong></div>
        <div><span>支付金额</span><strong>￥{{payAmt}}</strong></div>
        <div v-if="payType"><span>支付方式</span><strong>{{payType}}</strong></div>
        <div><span>创建时间</span><strong>{{createTime}}</strong></div>
        <div><span>订单编号</span><strong>{{orderSn}}</strong></div>
        <div v-if="status==2"><span>失败原因</span><strong>{{orderMessage}}</strong></div>
      </div>
      <payMethod v-if="status==2 && orderStatus!='cancel'" :mobile="mobile" :money="payAmt" :repay="true" :orderSn="orderSn"/>
    </div>
    <div class="recharge-tips">温馨提示：本业务不提供发票</div>
  </div>
</template>

<script>
// import util from '@/utils/utils'
import URL from '@/http/url'
import payMethod from './components/payMethod'
export default {
  name: 'rechargeresult',
  components: {
    payMethod
  },
  filters: {
    mobileFormate: function (phone) {
      if (!phone) return ''
      let arr = phone.split('')
      let str = ''
      arr.forEach(function(val, ind) {
        if (ind === 3 || ind === 7) {
          str += ' '
        }
        str += val
      })
      return str
    }
  },
  data() {
    return {
      status: 0, // 支付状态  1 支付成功 2 支付失败
      mobile: '', // 充值号码
      money: '', // 充值金额
      payAmt: '', // 支付金额
      orderSn: '', // 订单号
      createTime: '', // 创建时间
      paying: false, // 支付中
      payType: '', // 支付类型
      orderStatus: 'cancel', // 订单状态
      orderMessage: '', // 订单描述
    }
  },
  mounted () {
    this.status = this.$route.query.status
    this.orderSn = this.$route.query.orderSn
    if (this.$route.query.payType === 'weixin') {
      this.$dialog.confirm({
        title: '支付结果确认',
        message: '是否完成本次支付',
        confirmButtonText: '已完成',
        cancelButtonText: '重新支付'
      }).then(() => {
        this.$router.push('/rechargeresult?orderSn=' + this.orderSn)
      }).catch(() => {
        // on cancel
      });
    }
    this.fetchData()
  },
  methods: {
    fetchData() {
      let url = URL.GET_RECHARGE_DETAIL
      this.$http.get(`${url}?orderSn=${this.orderSn}&type=2`).then(res => {
        let result = res.data;
        this.mobile = result.data.billKey
        this.money = result.data.rechargeValue
        this.payAmt = result.data.payAmt
        this.createTime = result.data.orderTime
        this.orderSn = result.data.orderSn
        this.orderStatus = result.data.showStatus
        this.orderMessage = result.data.showStatusDesc
        this.payType = result.data.payType
        this.status = result.data.payStatus === 'paid' ? 1 : 2
      })
    },

  }
}
</script>

